  addBflyToStrategyBuilder(obj) {
    let type = 'CE';
    if (obj.scrip.includes('PE')) {
      type = 'PE';
    }
    let ltp = this.mapService.getScripLTP(this.oc_instru, this.expiryDate, obj.strike - this.strikeStepValue, type);
    this.addToStrategyBuilder(obj.strike - this.strikeStepValue, type, ltp, -this.bflyMonitorQty / 2);

    ltp = this.mapService.getScripLTP(this.oc_instru, this.expiryDate, obj.strike, type);
    this.addToStrategyBuilder(obj.strike, type, ltp, this.bflyMonitorQty);

    ltp = this.mapService.getScripLTP(this.oc_instru, this.expiryDate, obj.strike + this.strikeStepValue, type);
    this.addToStrategyBuilder(obj.strike + this.strikeStepValue, type, ltp, -this.bflyMonitorQty / 2);
  }

  addDSToStrategyBuilder(obj) {
    let type = 'CE';
    if (obj.scrip.includes('PE')) {
      type = 'PE';
    }
    
    let ltp = this.mapService.getScripLTP(this.oc_instru, this.expiryDate, obj.strike, type);
    this.addToStrategyBuilder(obj.strike, type, ltp, -this.dsMonitorQty);

    if (type === 'CE') {
      ltp = this.mapService.getScripLTP(this.oc_instru, this.expiryDate, obj.strike + this.strikeStepValue, type);
      this.addToStrategyBuilder(obj.strike + this.strikeStepValue, type, ltp, this.dsMonitorQty);
    } else {
      ltp = this.mapService.getScripLTP(this.oc_instru, this.expiryDate, obj.strike - this.strikeStepValue, type);
      this.addToStrategyBuilder(obj.strike - this.strikeStepValue, type, ltp, this.dsMonitorQty);
    }
  }

  addToStrategyBuilder(strike, type, ltp, qty = null) {
    if (!this.add_strangle_chart) {
      if (!qty) {
        qty = this.inlineOC_qty;
      }
      this.appService.payOffEvent$.next( { scrip: strike + type, ltp: ltp, qty: qty } );
    } else {
      if (type === 'CE') {
        this.strangle_chart_ce = strike + type;
      }
      if (type === 'PE') {
        this.strangle_chart_pe = strike + type;
      }
    }
    
  }

  addSLLOrder(strike, type, ltp) {

    let d = new Date(this.expiryDate);
    let monthNames = [ 'JAN', 'FEB', 'MAR', 'APR',
                      'MAY', 'JUN', 'JUL', 'AUG',
                      'SEP', 'OCT', 'NOV', 'DEC' ];
    let day = ('0' + d.getDate()).slice(-2);
    let monthIndex = d.getMonth();
    let monthName = monthNames[monthIndex];
    let year = d.getFullYear();
    let dateString = day + monthName + year;

    this.sl_trades.push({ qty: -this.inlineOC_qty, scrip: strike + type, expiry: dateString, price: ltp, trigger_price: ltp, order_id: null });
  }

  movePlaceOrder(index, upDown) {
    let to = index + upDown;

    // remove item at `index` and store it
    let f = this.place_orders.splice(index, 1)[0];
    // insert stored item into position `to`
    this.place_orders.splice(to, 0, f);
  }

