  calculatePnLAtPointForStrike(point, pos_strike, qty, entry, type) {
    let pnl = 0;
    if (type === PositionTypeForPayOff.CEsell) {
      if (point > pos_strike) {
        pnl = (pos_strike - point + entry) * Math.abs(qty);
      } else {
        pnl = entry * Math.abs(qty);
      }
    }
    if (type === PositionTypeForPayOff.PEsell) {
      if (point < pos_strike) {
        pnl = (-pos_strike + point + entry) * Math.abs(qty);
      } else {
        pnl = entry * Math.abs(qty);
      }
    }
    if (type === PositionTypeForPayOff.CEbuy) {
      if (point > pos_strike) {
        pnl = (-pos_strike + point - entry) * Math.abs(qty);
      } else {
        pnl = entry * -Math.abs(qty);
      }
    }
    if (type === PositionTypeForPayOff.PEbuy) {
      if (point < pos_strike) {
        pnl = (pos_strike - point - entry) * Math.abs(qty);
      } else {
        pnl = entry * -Math.abs(qty);
      }
    }

    // pnl = pnl * lotsize;

    if (pnl !== 0) {
      pnl = (Math.round((pnl + 0.00001) * 100) / 100);
    }
    return pnl;
  }

