  getQtyUptoFreezeLimit(instru, qty, roll_qty) {
    let freezeLimit = AppConstants.freezeLimit[instru];
    if (roll_qty && (Math.abs(roll_qty) < Math.abs(qty))) {
      if (freezeLimit < Math.abs(roll_qty)) {
        if (roll_qty < 0) {
          return -freezeLimit;
        } else {
          return freezeLimit;
        }
      } else {
        return roll_qty;
      }
    } else {
      if (freezeLimit < Math.abs(qty)) {
        if (qty < 0) {
          return -freezeLimit;
        } else {
          return freezeLimit;
        }
      } else {
        return qty;
      }
    }
    
  }

