  constructor(
    private route: ActivatedRoute,
    private appService: AppService,
    private mapService: MapService,
    private websocketService: WebsocketService,
    private chartService: ChartService,
    private angelService: AngelService,
    private angelWS: AngelWebsocketService,
    private holdingsService: HoldingsService,
    private zerodhaService: ZerodhaService,
    private zerodhaWebsocketService: ZerodhaWebsocketService,
    private algoService: AlgoService,
    /* private cdr: ChangeDetectorRef */) {
    this.route.queryParams.subscribe(params => {
      console.log('params', params);
      if (params && params.access_token) {
        this.access_token = params.access_token;
        // console.log('access_token', this.access_token);
        
       /*  this.getFunds();
        this.getPositions();
        this.getOrders(); */

        // this.getPayoff();
        
        // this.appService.getMastersContract();

        // this.angelLogin();
      }
      if (params && params.user_id) {
        this.user_id = params.user_id;
      }
    });

    AppConstants.requestStatusEvent$.subscribe(res => {
      console.log('app requestStatusEvent: ', res);
      this.lastRequestStatus = res;

      let status = res['status']; // success info warning danger primary secondary light dark
      let message = res['message'];

      let alert = {
        type: status,
        message: message,
      };

      this.alerts.unshift(alert);

      setTimeout(() => {
        this.alerts.splice(this.alerts.indexOf(alert), 1);
      }, 5000);
    });

    AppConstants.websocketEvent$.subscribe(res => {
      console.log('app websocketEvent: ', res);
      if (res === 'connected') {

        this.refreshAll();
        this.subscribeUSDINR();
        // this.subscribePositionsFeed();

        /* setTimeout(() => {
          this.startMonitoringGreeks();
        }, 10000); */
      }
    });

    AppConstants.miscEvent$.subscribe(res => {
      console.log('app miscEvent: ', res);
      if (res === 'refreshAll') {
        this.refreshAll();
        /* setTimeout(() => {
          this.refreshAll();
        }, 2500); */
      }
    });
  }

  log_ws_feed_change() {
    AppConstants.logWSFeed = this.log_ws_feed;
  }

  subscribeUSDINR() {

    let sub = [];
    let expiry = this.mapService.findCurrExpiryForDisplay(AppConstants.usdinrExpiries);
    let expiryString = this.mapService.getExpiry(expiry, 'USDINR');
    sub.push(this.mapService.getSubscriptionObjectForFuture('USDINR' + expiryString + 'FUT', 'CDS'));

    console.log('subscribeUSDINR feed', sub);
    this.appService.sendSubscriptions(sub); 
  }

  close(alert: Alert) {
    this.alerts.splice(this.alerts.indexOf(alert), 1);
  }

