  exportLongTermOptions() {
    // The program will keep downloading files after each iteration. The last file downloaded would have all the data.

    let d = new Date(this.longTermExpiryExport);  
    let expiryString = '' + d.getFullYear() + '-' + ('0' + (d.getMonth() + 1)).slice(-2) + '-' + ('0' + d.getDate()).slice(-2);
    
    let append = '';
    let daysToRetrieve = 550;
    
    let counter = 0;
    for (let i = 0; i < daysToRetrieve; i+=50) {

      setTimeout(() => {
        console.log('Starting to fetch expiry data');

        let to_date = new Date(d);
        to_date.setDate(d.getDate() - i);
        let toDateString = '' + to_date.getFullYear() + '-' + ('0' + (to_date.getMonth() + 1)).slice(-2) + '-' + ('0' + to_date.getDate()).slice(-2);

        let from_date = new Date(d);
        from_date.setDate(d.getDate() - i - 50);
        let fromString = '' + from_date.getFullYear() + '-' + ('0' + (from_date.getMonth() + 1)).slice(-2) + '-' + ('0' + from_date.getDate()).slice(-2);
    
        this.holdingsService.getZerodhaInstruments(append, AppConstants.broker_auth, fromString, toDateString, expiryString, true);
      }, counter * 120000);

      counter++;
    }
    
  }

