  mapOrders(res) {
    this.orders = [];
    let anyPending = false;
    let earlierCompleteOrders = AppConstants.completeOrders;
    AppConstants.completeOrders = 0;
    if (AppConstants.RestMode === 'zerodha') {
      let arr = res.data;

      // STATES:
    
      /* 
      https://kite.trade/docs/connect/v3/orders/#order-statuses
      The most common statuses are OPEN, COMPLETE, CANCELLED, and REJECTED.

      OPEN
      COMPLETE
      CANCELLED
      REJECTED

      An order can traverse through several interim and temporary statuses during its lifetime. For example, when an order is first placed or modified, it instantly passes through several stages before reaching its end state. Some of these are highlighted below.
      PUT ORDER REQ RECEIVED
      VALIDATION PENDING
      OPEN PENDING
      MODIFY VALIDATION PENDING
      MODIFY PENDING
      TRIGGER PENDING
      CANCEL PENDING
      AMO REQ RECEIVED
      */

      arr.forEach(element => {
        let o = new ZerodhaOrder();
        o.addToOrders = false;
        o.guid = element.guid;
        o.id = element.order_id;
        o.time = element.order_timestamp.split(' ')[1];
        let symbol = element.tradingsymbol;
        let parsed = this.mapService.parseSymbol(symbol);
        o.instru = parsed.instru;
        o.scrip = parsed.strike + parsed.type;
        let fromMasters = this.zerodhaService.findInMastersByToken(element.instrument_token);
        if (fromMasters && fromMasters.expiry) {
          o.expiry = fromMasters.expiry;
        }
        o.qty = element.quantity;
        if (element.transaction_type === 'SELL') {
          o.qty = -o.qty;
        }

        o.triggerPrice = element.trigger_price;

        o.status = element.status;
        if (o.status === 'COMPLETE') {
          AppConstants.completeOrders += 1;
        }
        if (o.status.includes('PENDING') && o.status !== 'TRIGGER PENDING') {
          anyPending = true;
        }

        if (/* o.triggerPrice */o.status.includes('TRIGGER PENDING')) {
          // console.log('algoss inside status-', o.status, o.scrip);
          o.tradedPrice = element.price;
        } else {
          o.tradedPrice = /* element.price != 0 ? element.price : */ element.average_price;
        }

        o.message = element.status_message;
        o.tradingsymbol = element.tradingsymbol;
        if (element.tags) {
          o.tags = '';
          element.tags.forEach(e => {
            o.tags += e + ',';
          });
          o.tags = o.tags.replace(/,\s*$/, '');
        } else {
          o.tags = '';
        }
        this.orders.push(o);
      });
      this.sortOrders();
      AppConstants.orders = this.orders;
    }
    this.completeOrders = AppConstants.completeOrders;
    if (earlierCompleteOrders + AppConstants.placedOrders > this.completeOrders) {
      // AppConstants.requestStatusEvent$.next({'status': 'warning', 'message': 'Orders mismatch. Refetching...'});
    } else {
      AppConstants.placedOrders = 0;
    }
    if (anyPending) {
      AppConstants.requestStatusEvent$.next({'status': 'warning', 'message': 'Order Pending. Refetching...'});
      setTimeout(() => {
        if (!AppConstants.enableAlgo) {
          this.refreshAll();
        }
      }, 2000);
    }

    if (AppConstants.RestMode === 'fyers') {
      if (res.orderBook instanceof Array) {
        res.orderBook.forEach(order => {
          let o = new Order();
          o.id = order.id;
          o.scrip = order.symbol;
          o.qty = order.qty;
          o.status = order.status;
          o.message = order.message;
          o.type = order.type;
          o.limit = order.limitPrice;
          o.stop = order.stopPrice;
          o.tradedPrice = order.tradedPrice;
          o.side = order.side;
          o.orderDateTime = order.orderDateTime;
          this.orders.push(o);
        });
      }
    }
  }

