  place_order(symbol, qty, access_token, limit_price = null, orderId = null, product_type = "NRML", tag = AppConstants.defaultTag, trigger_price = 0) {

    //BANKNIFTY2231034500CE
    //BANKNIFTY22MAR35000CE
    
    //USDINR2231177CE
    //USDINR22MAR78.25CE

    /* 
    # Products
    PRODUCT_MIS = "MIS"
    PRODUCT_CNC = "CNC"
    PRODUCT_NRML = "NRML"
    PRODUCT_CO = "CO"
    PRODUCT_BO = "BO"

    # Order types
    ORDER_TYPE_MARKET = "MARKET"
    ORDER_TYPE_LIMIT = "LIMIT"
    ORDER_TYPE_SLM = "SL-M"
    ORDER_TYPE_SL = "SL"

    # Varities
    VARIETY_REGULAR = "regular"
    VARIETY_BO = "bo"
    VARIETY_CO = "co"
    VARIETY_AMO = "amo"

    # Transaction type
    TRANSACTION_TYPE_BUY = "BUY"
    TRANSACTION_TYPE_SELL = "SELL"

    # Validity
    VALIDITY_DAY = "DAY"
    VALIDITY_IOC = "IOC"

    # Exchanges
    EXCHANGE_NSE = "NSE"
    EXCHANGE_BSE = "BSE"
    EXCHANGE_NFO = "NFO"
    EXCHANGE_CDS = "CDS"
    EXCHANGE_BFO = "BFO"
    EXCHANGE_MCX = "MCX"

    # Margins segments
    MARGIN_EQUITY = "equity"
    MARGIN_COMMODITY = "commodity"

    # Status constants
    STATUS_COMPLETE = "COMPLETE"
    STATUS_REJECTED = "REJECTED"
    STATUS_CANCELLED = "CANCELLED"

    # GTT order type
    GTT_TYPE_OCO = "two-leg"
    GTT_TYPE_SINGLE = "single"

    # GTT order status
    GTT_STATUS_ACTIVE = "active"
    GTT_STATUS_TRIGGERED = "triggered"
    GTT_STATUS_DISABLED = "disabled"
    GTT_STATUS_EXPIRED = "expired"
    GTT_STATUS_CANCELLED = "cancelled"
    GTT_STATUS_REJECTED = "rejected"
    GTT_STATUS_DELETED = "deleted" */

    let type = "BUY";
    if (qty < 0) {
      type = "SELL";
      qty = Math.abs(qty);
    }

    let order_type = 'MARKET';
    let price = 0;
    if (limit_price) {
      order_type = 'LIMIT';
      price = limit_price;
    }
    if (trigger_price) {
      order_type = 'SL';
    }

    // access_token = 'enctoken FCvYj7w29KmLjExNRLGTkibQadSEe1hilUOWjGs9cMn18jXW6JZN1Afxgw2tCCU+0G4WiB9eIO6Hl887gU/ellNwiYEE/ASez3YDlCERAq7EgsQ7mfZmkQ==';

    let httpOptions = {
      headers: new HttpHeaders({
        Authorization: access_token,
        'Content-Type': 'application/x-www-form-urlencoded'
      }),
      responseType: 'json' as 'json'
    };

    let exchange = "NFO";
    if (symbol.includes("SENSEX") || symbol.includes("BANKEX")) {
      exchange = "BFO";
    }

    let url = this.baseUrl + 'oms/orders/regular';

    if (orderId) {
      let b = new HttpParams()
      .set("variety", "regular")
      .set("exchange", exchange)
      .set("tradingsymbol", symbol) /* "BANKNIFTY2231034500CE" */
      .set("transaction_type", type)
      .set("order_type", "" + order_type)
      .set("quantity", qty) /* "25" */
      .set("price", "" + price)
      .set("product", product_type)
      .set("validity", "DAY")
      .set("disclosed_quantity", "0")
      .set("trigger_price", "" + trigger_price)
      .set("squareoff", "0")
      .set("stoploss", "0")
      .set("trailing_stoploss", "0")
      .set("user_id", AppConstants.zerodhaUserId)
      .set("order_id", orderId)
      .set("parent_order_id", "");

      url += '/' + orderId;

      return this.http.put(url, b.toString(), httpOptions);
    } else {

      let b = new HttpParams()
      .set("variety", "regular")
      .set("exchange", exchange)
      .set("tradingsymbol", symbol) /* "BANKNIFTY2231034500CE" */
      .set("transaction_type", type)
      .set("order_type", "" + order_type)
      .set("quantity", qty) /* "25" */
      .set("price", "" + price)
      .set("product", product_type)
      .set("validity", "DAY")
      .set("disclosed_quantity", "0")
      .set("trigger_price", "" + trigger_price)
      .set("squareoff", "0")
      .set("stoploss", "0")
      .set("trailing_stoploss", "0")
      .set("user_id", AppConstants.zerodhaUserId)
      .set("tag", tag);

      // {"status":"success","data":{"order_id":"231023302678937"}}
      return this.http.post(url, b.toString(), httpOptions);
    }
  }

  cancel_order(order_id) {
    // https://kite.zerodha.com/oms/orders/regular/231024400007652?order_id=231024400007652&parent_order_id=&variety=regular

    let access_token = AppConstants.broker_auth;

    let httpOptions = {
      headers: new HttpHeaders({
        Authorization: access_token,
        'Content-Type': 'application/json'
      }),
      responseType: 'json' as 'json'
    };

    let url = 'https://kite.zerodha.com/oms/orders/regular/' + order_id + '?order_id=' + order_id + '&parent_order_id=&variety=regular';
    return this.http.delete(url, httpOptions);
  }

