  showOILimit(instru, strike) {

    if (instru === 'NIFTY' && AppConstants.nfCEOILimit && AppConstants.nfPEOILimit) {
      if (strike > AppConstants.nfCEOILimit || strike < AppConstants.nfPEOILimit) {
        return true;
      }
    }

    if (instru === 'BANKNIFTY' && AppConstants.bnfCEOILimit && AppConstants.bnfPEOILimit) {
      if (strike > AppConstants.bnfCEOILimit || strike < AppConstants.bnfPEOILimit) {
        return true;
      }
    }

    return false;
  }

