  subscribe(tokens) {
		if(tokens.length > 0) {
			this.send({"a": this.mSubscribe, "v": tokens});
		}
		return tokens;
	};

  setMode(mode, tokens) {
    if (!mode) {
      mode = this.modeFull;
    }
		if(tokens.length > 0) {
			this.send({"a": this.mSetMode, "v": [mode, tokens]});
		}
		return tokens;
	};

